export const SET_USERNAME = 'SET_USERNAME'
export const UPDATE_CHAT_LOG = 'UPDATE_CHAT_LOG'

export function setUsername (username) {
  return {
    type: SET_USERNAME,
    payload: username
  }
}

export function updateChatLog (update) {
  return {
    type: UPDATE_CHAT_LOG,
    payload: update
  }
}
